.sign__heading{
    margin:60px auto 50px;
    padding: 0;
    width: 330px;
    text-align: center;
    font-family: inherit;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 0.95;
    color: #fff;
}

@media screen and (max-width: 501px) {
    .sign__heading {
        margin: 25px auto 75px 22px;
        width: 238px;
        font-size: 18px;
        line-height: 1.22;
    }
}