.popup {
    display: flex;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    transition: opacity .4s ease-in, visibility .4s ease-out;
    justify-content: center;
    align-items: center;
}
