@font-face {
    font-family: 'Inter';
    font-weight: regular;
    font-weight: 400;
    font-style:  normal;
    src: local("Inter-Regular"),
    local("Inter"),
    url("Inter-Regular.woff2") format("woff2"),
    url("Inter-Regular.woff") format("woff"),
    url("Inter-Regular.woff?v=3.15") format("woff");
    }

